import React from 'react'
import { Link } from 'react-router-dom'
import TaskListPending from '../task/TaskListPending'
import CommentListPending from '../comments/CommentListPending'

const Admin = ({ handleAddTask, handleUpdateTask, handleDeleteTask }) => (
  <div>
    <TaskListPending handleAddTask={handleAddTask} handleUpdateTask={handleUpdateTask} handleDeleteTask={handleDeleteTask} />
    <CommentListPending/>
    <div className="admin-list"><Link className="no-underline" to="/omasivu">Omat tiedot</Link></div>
    <div className="admin-list"><Link className="no-underline" to="/lisaa_admin">Lis yllpitj</Link></div>
    <div className="admin-list"><Link className="no-underline" to="/lisaa_pudotusvalikkoon">Hallinnoi sntj, kieli, sarjoja ja kategorioita</Link></div>
  </div>
)

export default Admin
